import React, { Component } from "react";
import PageLoading from "../components/PageLoading";
import PageError from "../components/PageError";
import PokemonList from "../components/PokemonList";
import MenuRight from "../components/MenuRight";
import MiniLoader from "../components/MiniLoader";

import "./styles/Pokemon.css";

export default class Pokemon extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      MiniLoader: false,
      error: null,
      data: [],
      type: false,
      search_pokemon: ''
    };
  }

  componentDidMount() {
    this.getAllPokemon();
  }

  getAllPokemon = async () => {
    this.setState({ loading: true, error: null });

    try {
      const getPokemon = await fetch(
        // "https://pokeapi.co/api/v2/pokemon?offset=0&limit=5"
        "https://pokeapi.co/api/v2/pokemon"
      );
      const data = await getPokemon.json();
      this.setState({
        loading: false,
        data: data,
        type: false
      });
      // console.log(this.state.data[0]);
      // console.log(this.state.data);
    } catch (error) {
      this.setState({
        loading: false,
        error: error,
        type: false
      });
    }
  };

  getMorePokemon = async () => {
    this.setState({ MiniLoader: true, error: null });

    try {
      const getPokemon = await fetch(this.state.data.next);
      const data = await getPokemon.json();
      // console.log(this.state.data);
      this.setState({
        MiniLoader: false,
        data: {
          next: data.next,
          results: [...this.state.data.results, ...data.results]
        },
        type: false
      });
      // console.log(this.state.data);
    } catch (error) {
      this.setState({ MiniLoader: false, error: error });
    }
  };

  getTypePokemon = async e => {
    const url = e.target.getAttribute("url");

    this.setState({ loading: true, error: null });

    try {
      const getPokemon = await fetch(url);
      const data = await getPokemon.json();
      this.setState({
        loading: false,
        data: data.pokemon,
        type: true
      });

        // console.log(this.state.data);
      //   console.log(this.state.data[0].pokemon);
    } catch (error) {
      this.setState({
        loading: false,
        error: error
      });
    }
  };

  //onChange to search ..
  /*
  onChange = async (e) =>{
    await this.setState({
      [e.target.name] : e.target.value
    });

    if(this.state.data.results){
      const filter = this.state.data.results.filter(result => {
        return `${result.name}`
          .toLowerCase()
          .includes(this.state.search_pokemon);
      });

      console.log(this.state.data.results);

      this.setState({
        data: {
          results: filter
        }
      })
    }

    console.log(this.state.data.results);

    // console.log( this.state.search_pokemon);
  } */


  render() {
    let contenido;
    if (this.state.loading) {
      contenido = <PageLoading></PageLoading>;
    }else if(this.state.MiniLoader){
      contenido = (<React.Fragment>
        <PokemonList
          data={this.state.data}
          type={this.state.type}
        ></PokemonList>
        <MiniLoader></MiniLoader></React.Fragment>
      );
    } else {
      contenido = (
        <PokemonList
          data={this.state.data}
          type={this.state.type}
        ></PokemonList>
      );
    }

    if (this.state.error) {
      return <PageError error={this.state.error} />;
    }

    return (
      <section className="Pokemon container-fluid">
        <div className="row">
          <div className="col-12 col-md-8">
            <div className="Pokemon__pokemon-content row">
              {contenido}
              {!this.state.loading && this.state.data.next && (
               <div className="w-100 p-2"><button className="btn btn-dark w-100" onClick={() => this.getMorePokemon()}>Load More</button></div> 
              )}
            </div>
          </div>
          <div className="col-12 col-md-4">
            <MenuRight
              onChange={this.onChange}
              search_pokemon= {this.state.search_pokemon}
              getAllPokemon={this.getAllPokemon}
              getTypePokemon={this.getTypePokemon}
            ></MenuRight>
          </div>
        </div>
      </section>
    );
  }
}
