import React from "react";
import "./styles/CardMini.css";
import { Link } from "react-router-dom";


export default function CardMini(props) {

  const name = props.pokemon.pokemon.name;
  const img = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${props.pokemon.id}.png`;

  // console.log("ok");

  return (
        
        <div className="col-6 col-md-4 col-lg-3 CardMini">
          <div className="CardMini__container">
            <figure>
              <img src={img} alt=""/>
            </figure>
              <h5>{name}</h5>
            <Link to={`/pokemon/${props.pokemon.id}`}
             className="btn btn-outline-dark">Info</Link>
          </div>
        </div>

  );
}
