import React, { Component } from "react";
import CardMini from "./CardMini";

export default class PokemonList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      type: props.type,
      pokemon : ''
    };

    // console.log(props);}
  }

  componentDidMount(){
  }
  

  render() {
    if (this.state.type) {
        return this.state.data.map((pokemon, index) => {
          const getId = pokemon.pokemon.url.replace("https://pokeapi.co/api/v2/pokemon/","");
          const id = getId.replace("/","")
          // console.log(pokemon);
            
            return <CardMini key={index}  pokemon={{id: id, pokemon: pokemon.pokemon}}></CardMini>;
          });
    }
    
    return this.state.data.results.map((pokemon, index) => {
       
        const getId = pokemon.url.replace("https://pokeapi.co/api/v2/pokemon/","");
        const id = getId.replace("/","")
        return <CardMini key={id} pokemon={{id: id, pokemon: pokemon}}></CardMini>;
    });
  }
}
