import React from "react";
import MiniLoader from "./MiniLoader";
import "./styles/MenuRight.css";

export default class MenuRight extends React.Component {
  state = {
    loading: true,
    error: null,
    data: [],
    menuRightToggle: false
  };

  componentDidMount() {
    this.getListTypePokemon();
  }

  async getListTypePokemon() {
    this.setState({ loading: true, error: null });

    try {
      const getListType = await fetch("https://pokeapi.co/api/v2/type");
      const data = await getListType.json();
      this.setState({
        loading: false,
        data: data.results
      });

      //   console.log(this.state.data);
    } catch (error) {
      this.setState({
        loading: false,
        error: error
      });
    }
  }

  handleSelect = e => {
    // console.log(e.target.parentElement);

    let MenuRight__selected = document.getElementsByClassName(
      "MenuRight__selected"
    );

    if (MenuRight__selected.length >= 1) {
      document
        .querySelectorAll(".MenuRight__selected")
        .forEach(e => e.classList.remove("MenuRight__selected"));
    }

    if (MenuRight__selected.length === 0) {
      e.target.parentElement.classList.add("MenuRight__selected");
    }

    // this.MenuRightToggle();
    const element = document.getElementById("MenuRight");

    if(this.state.menuRightToggle){
     this.MenuRightToggle();
    }
  };

  MenuRightToggle = async () =>{
    await this.setState({menuRightToggle : !this.state.menuRightToggle})
    console.log(this.state.menuRightToggle)
    
    const element = document.getElementById("MenuRight");

    if(this.state.menuRightToggle){
      element.classList.add("MenuRight__Top");
    }else{
      element.classList.remove("MenuRight__Top");
    }

  }

  
  render() {

    const showMenuToggle = {
      top: "5%"
    };

    return (
      <div id="MenuRight" className="MenuFlotante" style={ this.state.MenuRightToggle ? showMenuToggle : null }>
        {this.state.loading && <MiniLoader />}
        <div className="d-sm-block d-md-none MenuRight__toggle" onClick={this.MenuRightToggle}>Menu</div>
        <ul id="MenuRight__list">
          {/* <div className="form-group">
            <input
              onChange={this.props.onChange}
              className="form-control"
              type="text"
              name="search_pokemon"
              value={this.props.search_pokemon}
              placeholder="Search"
            />
          </div> */}
         
          <li
            key="0"
            onClick={this.handleSelect}
            className="MenuRight__selected"
          >
            <p onClick={this.props.getAllPokemon}>Todos</p>
          </li>
          {this.state.data.map((type, index) => {
            return (
              <li key={index} onClick={this.handleSelect}>
                <p onClick={this.props.getTypePokemon} url={type.url}>
                  {type.name}
                </p>
              </li>
            );
          })}
        </ul>
      </div>
    );
  }
}
