import React, { Component } from "react";
import PageLoading from "../components/PageLoading";
import PageError from "../components/PageError";
import "./styles/PokemonDetail.css";

export default class PokemonInfo extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      error: null,
      pokemon: undefined
    };
  }

  componentDidMount() {
    this.getPokemonInfo();
  }

  getPokemonInfo = async () => {
    // console.log(this.props.match.params.id);
    this.setState({ loading: true, error: false });

    try {
      const data = await fetch(
        "https://pokeapi.co/api/v2/pokemon/" + this.props.match.params.id
      );
      const pokemon = await data.json();

      this.setState({ loading: false, pokemon });

      // console.log(this.state.pokemon.sprites.front_default)
    } catch (error) {
      this.state({ loading: false, error });
    }
  };

  render() {
    if (this.state.loading || this.state.pokemon === undefined) {
      return <PageLoading></PageLoading>;
    }

    if (this.state.error) {
      return <PageError error={this.state.error} />;
    }

    const pokemon = this.state.pokemon;
    // console.log(pokemon.sprites);

    return (
      <div className="PokemonInfo">
        <div className="PokemonInfo__content container">
              <figure>
                <img className="PokemonInfo_main-img" src={pokemon.sprites.front_default} alt="pokemon" />
              </figure>
             
              <h3 className="mb-5 PokemonInfo__name">{pokemon.name.toUpperCase()}</h3>
              
              <div className="w-100">
                    <div className="row">
                        <div className="col-4">
                        <h5>ABILITY:</h5>
                            {
                                pokemon.abilities.map( (ability, index) => {
                                    return (
                                    <div key={index}>
                                        <p>‚Ä¢ {ability.ability.name}</p>
                                    </div>
                                    )
                                } )
                            }
                        </div>
                        <div className="col-4">
                            <div>
                                <h5>HEIGHT</h5>
                                <p>{pokemon.height}</p>
                            </div>
                            <div>
                                <h5>WEIGHT</h5>
                                <p>{pokemon.weight}</p>
                            </div>
                        </div>
                        <div className="col-4">
                            <h5>TYPES</h5>
                            {
                                pokemon.types.map( (type, index) => {
                                    return (
                                    <div key={index}>
                                        <p>‚Ä¢ {type.type.name}</p>
                                    </div>
                                    )
                                } )
                            }
                        </div>
                    </div>
              </div>
              </div>
          </div>
    );
  }
}
